.App {
  display: grid;
  grid-template-columns: 800px;
  justify-content: center;
  gap: 10px;

  font-family: Roboto, sans-serif;;
}

.site__title {
  text-align: center;
  color: #4D80AA;
}

.article {
  border: 1px solid black;
  padding: 10px;
}

.article__title {
  text-align: center;
}

.article__body {
  padding: 0 10px;
}

.article__author {
  text-align: right;
  font-size: 12px;
}

.comment__author {
  font-size: 12px;
  font-weight: bold;
  padding: 10px 0;
}

.comment__body {
  font-size: 13px;
}